import request from "@/utils/request";
import store from '@/config/store';
import md5 from '@/utils/md5';
import {
	utf8
} from '@/utils/utils';
import base from '@/config/baseUrl';
let $http = new request({
	baseUrl: base.baseUrl,
	fileUrl: base.imgBaseUrl,
	headers: {
		'content-type': 'application/x-www-form-urlencoded'
	}
});
let requestNum = 0;
//
$http.requestStart = function (options, data) {
	if (requestNum <= 0) {
		uni.showNavigationBarLoading();
		if (options.load) {
			//
			uni.showLoading({
				title: ''
			});
		}
	}
	requestNum += 1;
	var callbackData = {};
	//sessionId
	if (store.state.sessionId) {
		callbackData.options = {
			headers: {
				...options.headers,
				'Cookie': store.state.sessionId
			}
		};
	};
	console.log("", options, callbackData);
	return callbackData;
}
//
$http.requestEnd = function (options, resolve) {
	//
	requestNum = requestNum - 1;
	if (requestNum <= 0) {
		uni.hideLoading();
		uni.hideNavigationBarLoading();
	}
}
let loginPopupNum = 0;
//
$http.dataFactory = function (options, resolve) {
	console.log("", resolve);
	//
	var callback = {
		success: false,
		result: ""
	};
	//
	if (resolve.data.result == 0) {
		callback.success = true;
		callback.result = resolve.data.data;
	} else if (resolve.data.result == 10403) { //code == 10403 
		var content = '';
		if (store.state.userInfo.is_real && store.state.userInfo.is_real == 2) {
			content = '';
		}
		if (loginPopupNum <= 0) {
			loginPopupNum++;
			uni.showModal({
				title: '',
				content: content,
				confirmText: "",
				cancelText: "",
				success: function (res) {
					loginPopupNum--;
					if (res.confirm) {
						//
						$http.openLogin && $http.openLogin();
					}
				}
			});
		}
	} else { //
		if (options.isPrompt) {
			setTimeout(function () {
				uni.showToast({
					title: resolve.data.res_info,
					icon: "none",
					duration: 3000
				});
			}, 500);
		}
		callback.result = resolve.data;
	}
	return callback;
};
export default $http;
